@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/fira-sans-400.woff2") format("woff2"), url("../fonts/fira-sans-400.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/fira-sans-500.woff2") format("woff2"), url("../fonts/fira-sans-500.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap; }

@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/fira-sans-700.woff2") format("woff2"), url("../fonts/fira-sans-700.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

html,
body {
  padding: 0;
  margin: 0; }

body {
  min-width: 320px;
  font-size: 17px;
  line-height: 1.7;
  font-family: "Fira Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  background-size: 100%;
  background-repeat: no-repeat; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@media (min-width: 768px) {
  body {
    background-image: url("../img/bg-hero.jpg"); } }

@media (min-width: 768px) and (-webkit-min-device-pixel-ratio: 1.5), (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  body {
    background-image: url("../img/bg-hero@2x.jpg"); } }

@media (max-width: 1360px) {
  body {
    background-position: top center; } }

@media (max-width: 767px) {
  body {
    background-image: url("../img/bg-hero-small.jpg");
    background-size: 912px auto; } }

@media (max-width: 767px) and (-webkit-min-device-pixel-ratio: 1.5), (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  body {
    background-image: url("../img/bg-hero-small@2x.jpg"); } }

.container {
  min-width: 320px;
  max-width: 1142px;
  margin: 0 auto;
  padding: 0 20px; }

.header {
  width: 100%;
  padding: 32px 0;
  font-weight: 500;
  font-size: 15px; }

.header__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__tel {
  color: #000000;
  text-decoration: none; }

.header__tel:hover {
  color: #ffc341; }

.header__callback {
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
  border-bottom: 1px dashed #000000; }

.header__callback:hover {
  color: #ffc341;
  border-color: #ffc341; }

.hero {
  padding-top: 416px;
  padding-bottom: 146px; }

.hero__description {
  width: 350px;
  margin: 0 auto 32px;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.2;
  color: #ffffff;
  text-align: center; }

.hero__scroll-indicator {
  position: relative;
  display: block;
  width: 76px;
  height: 76px;
  margin: 0 auto calc(131px - 76px);
  background-image: url("../img/dashed-round.svg");
  background-repeat: no-repeat;
  background-size: 76px 76px;
  background-position: top center; }

.hero__scroll-indicator::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 52px;
  height: 52px;
  margin-top: -27px;
  margin-left: -27px;
  background-image: url("../img/icon-mouse.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 17px 26px;
  border-radius: 50%;
  border: 1px solid #ffffff; }

.hero__scroll-indicator::after {
  content: "";
  position: absolute;
  top: 76px;
  left: 50%;
  width: 1px;
  height: 55px;
  background-color: #ffffff; }
